.contacts-info {
    display: flex;
    margin-bottom: 80px;
    justify-content: space-between;
}

.contacts-info p {
    font-size: 16px;
    font-weight: lighter;
}

.contacts-info a {
    text-decoration: none;
    font-size: 16px;
    color: #252525;
    font-weight: 200;
}

.contacts-info a:hover {
    color: #E0BEA2;
}

.contacts-info svg:hover path {
    fill: #252525;
}

.contacts-email {
    text-align: center;
}

.contacts-email h2 {
    margin: 0 0 40px;
    font-weight: 300;
    font-size: 36px;
    line-height: 42px;
    color: #252525;
}

.contacts-email-text {
    padding-top: 20px;
    font-weight: lighter !important;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #252525;
    width: 600px;
}

@media (max-width: 821px)  {
    .contacts-info {
        margin-bottom: 50px;
    }

    .contacts-info a {
        font-size: 13px;
    }

    .contacts-info li {
        margin-right: 30px;
    }

    .contacts-info p {
        font-size: 13px;
    }

    .contacts-email h2 {
        font-size: 32px;
    }
}

@media (max-width: 420px) {
    .contacts-info {
        display: flex;
        flex-wrap: wrap;
    }

    .contacts-info p {
        margin-bottom: 0;
        font-weight: 300;
    }

    .contacts-info a {
        font-weight: 300;
    }

    .contacts-info li {
        margin-right: 40px;
        margin-bottom: 20px;
    }

    .contacts-email-text {
        font-weight: 200 !important;
        font-size: 14px;
        line-height: 16px;
        width: 335px;
    }
}

