/* hero and header */
.hero {
    background-image: url(/src/Assets/Фон.png);
    height: 900px;
}

.header-main {
    margin: 0 150px 0;
    font-size: 16px;
    font-weight: 100;
    color: white;
}

.header-main-nav {
    display: flex;
    align-items: center;
    padding-top: 24px;
}

.header-main-nav-link {
    text-decoration: none;
    color: white;
}

.header-main-nav li {
    margin-right: 60px;
}

.header-logo {
    margin-left: 60px;
    margin-right: 120px;
}

.header-main-nav-link:hover {
    cursor: pointer;
    color: #E0BEA2;
}

.header-main-nav li:hover {
    cursor: pointer;
    color: #E0BEA2;
}

.header-main-controls button:hover path {
    cursor: pointer;
    fill:#E0BEA2;
}

.header-main-controls button {
    padding-right: 10px;
    padding-left: 10px;
    background: transparent;
    border: none;
    cursor: pointer;
}

.hero-text {
    margin-top: 329px;
    text-align: center;
}

.hero-link {
    text-decoration: none;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}

.hero-link svg {
    margin-left: 5px;
}

.hero-link:hover {
    color: #E0BEA2;
}

.hero-link:hover svg path {
    fill: #E0BEA2;
}

.hero-text h1 {
    margin: 0;
    font-style: normal;
    font-weight: 300;
    font-size: 46px;
    line-height: 54px;
    color: white;
}

.hero-text p {
    justify-content: center;
    display: flex;
    align-items: center;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    color: white;
}

/* main links */

.main-category {
    margin: 100px 150px ;
}

.main-category h1 {
    margin: 100px 0 50px;
    font-weight: 300;
    font-size: 36px;
    line-height: 42px;
    color: #252525;
}

.main-category-link {
    display: flex;
    justify-content: space-between;
}

.main-category-link a {
    font-weight: 300;
    font-size: 20px;
    line-height: 23px;
    color: #FFFFFF;
    text-decoration: none;
    height: 450px;
    width: 273px;
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.main-category-link a:first-child {
    margin-left: 0;
}

.link-kurtka {
    background-image: url(/src/Assets/Куртки.png);
}

.link-palto {
    background-image: url(/src/Assets/Пальто.png);
}

.link-shuba {
    background-image: url(/src/Assets/Шубы.png);
}

.link-parka {
    background-image: url(/src/Assets/Парки.png);
}

.link-background {
    background: rgba(224, 190, 162, 0.75);
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* main email */

.main-email {
    text-align: center;
}

.main-email h2 {
    margin: 0 0 50px;
    font-weight: 300;
    font-size: 36px;
    line-height: 42px;
    color: #252525;
}

.form-controls {
    display: flex;
    flex-direction: column;
    align-items: center
}

.form-input {
    width: 600px;
    text-align: center;
    border: 0.5px solid #252525;
    padding: 16px 0 15px;
    font-weight: 200;
    font-size: 16px;
    line-height: 19px;
    color: #252525;
}

.form-input::placeholder {
    color: rgba(37, 37, 37, 0.5);
}

.form-button {
    cursor: pointer;
    width: 600px;
    margin-top: 20px;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: white;
    background: #E0BEA2;
    border: none;
    padding: 16px 81px 17px;
}

.form-button:hover {
    background: #CCA88A;
}

.main-email p {
    font-weight: 200;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #252525;
    width: 600px;
}
@media (max-width: 821px) {
    /* hero and header */
    .header-main {
        margin: 0 60px 0;
    }

    .header-logo {
        height: 30px;
        margin: 0 7px 0 0;
    }

    .header-main-nav {
        justify-content: start;
        font-size: 15px;
    }
    .header-main-nav li {
        margin-right: 13px;
    }

    /* main links */
    .main-category {
        margin: 100px 60px ;
    }

    .link-kurtka, .link-palto, .link-shuba,
    .link-parka {
        background-position: center;
    }
}

@media (max-width: 420px) {
    /* hero and header */
    body {
        overflow-x: hidden;
    }

    ::-webkit-scrollbar {
        display: none;
    }

    .hero {
        background-image: url(/src/Assets/heroPhone.png);
        height: 667px;
        background-repeat: no-repeat;
        background-size: cover;
        z-index: -1;
    }

    .header-main {
        margin: 0;
    }

    .header-main-nav {
        padding: 26px 20px 0;
        justify-content: space-between;
    }

    .header-main-nav-link {
        display: none;
    }

    .header-main-nav-link {
        display: none;
    }

    .header-main-controls  .header-main-nav-link  {
        display: unset;
    }

    .header-main-nav svg {
        height: 20px;
    }

    .header-main-controls {
        margin: 0;
    }

    .header-main-controls button {
        padding: 0;
    }

    .header-main-controls button:last-child {
        padding-left: 10px;
    }

    .hero-text {
        margin-top: 246px;
    }

    .hero-text h1 {
        font-size: 32px;
        font-weight: lighter;
    }

    .hero-text p {
        font-weight: 300;
        font-size: 14px;
    }

    /* main links */

    .main-category {
        margin: 60px 20px ;
    }

    .main-category h1 {
        margin: 0 0 30px;
        font-size: 24px;
    }

    .main-category-link {
        flex-wrap: wrap;
    }

    .main-category-link a {
        width: 165px;
        height: 230px;
        margin-left: 0;
        margin-bottom: 5px;
    }

    .link-kurtka, .link-palto, .link-shuba,
    .link-parka {
        background-size: cover;
    }

    /* main links */

    .main-email h2 {
        margin: 0 0 30px;
        font-size: 24px;
    }

    .form-input {
        width: 335px;
    }

    .form-button {
        width: 335px;
    }

    .main-email p {
        width: 335px;
        font-size: 14px;
    }
}
