.header-main-burger {
    display: none;
    background: transparent;
    border: none;
}

.burger-btn {
    padding: 0;
    border: none;
    background: transparent;
}

body .burger-container {
    overflow-y: hidden;
}

.burger-container {
    margin-top: 15px;
    background: #FFFFFF;
    width: 100%;
    height: 620px;
}

.burger-container-list {
    font-weight: lighter;
    font-size: 22px;
    line-height: 19px;
    text-align: center;
    color: #252525;
    padding: 10px 20px 0;
}

.burger-container-list a {
    text-decoration: none;
    color: #252525;
}

.burger-container-list li {
    border-bottom:  0.3px solid #252525;
    padding-top: 20px;
    padding-bottom: 20px;
}


@media (max-width: 420px) {

    .header-main-burger {
        display: unset;
    }

    svg.header-logo {
        margin-left: 4px;
        width: 150px;
    }

    .burger-footer-rights {
        position: absolute;
        z-index: 10;
        margin-top: 190px;
        text-align: center;
        right: 27%;
    }

    .burger-footer-rights a {
        text-decoration: none;
        font-weight: 200;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        color: #252525;
    }

    .burger-footer-rights span {
        font-weight: 200;
        font-size: 14px;
        line-height: 16px;
    }

    .burger-footer-rights li {
        text-align: center;
    }
}
