@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;300;400;600&display=swap');

::-webkit-scrollbar {
 width: 0;
}

.app {
 margin: 0;
 max-width: 1440px;
 font-family: 'Raleway', sans-serif;
 font-style: normal;
}

select {
 font-family: 'Raleway', sans-serif;
}

body{
 margin: 0;
 padding: 0;
}

ul {
 padding-left: 0;
 list-style-type: none !important;
 margin-block-start: 0;
 margin-block-end: 0;
}

li {
 text-decoration: none;
}

a {
 text-decoration: none;
}

