.footer-container {
    text-align: center;
    margin-bottom: 10px;
}

.footer-burger {
    display: none;
}

.footer-normal {
    display: unset;
}

.footer {
    text-align: start;
    display: flex;
    margin: 100px 150px 74px;
    justify-content: space-between;
}

.footer h3 {
    font-weight: 300;
    font-size: 20px;
    line-height: 23px;
    color: #252525;
    margin: 0 0 20px;
}

.footer a {
    text-decoration: none;
    font-weight: 200;
    font-size: 16px;
    line-height: 19px;
    color: #252525;
}

.footer a:hover {
    color: #E0BEA2;
}

.footer svg:hover path {
    fill: #252525;
}

.footer li {
    padding-bottom: 10px;
}

.footer-svg {
    margin-right: 10px;
}

.footer-rights {
    text-align: center;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    color: #252525;
    text-decoration: none;
}

.footer-rights a {
    color: #252525;
    text-decoration: none;
    cursor: pointer;
}

@media (max-width: 821px) {
    .footer {
        margin: 80px 60px 50px;
    }

    .footer-rights {
        margin-bottom: 20px;
    }
}

@media (max-width: 420px)  {
    .footer {
        margin: 60px 20px 0;
    }

    .footer-burger {
        display: unset;
        margin: 60px 20px 0;
    }

    .footer-normal {
        display: none;
    }

    .footer-burger-contacts h3 {
        font-weight: 300;
        font-size: 18px;
        line-height: 21px;
        text-align: center;
        color: #252525;
        margin: 20px 0 10px;
    }

    .footer-burger-contacts a {
        text-decoration: none;
        font-weight: 200;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        color: #252525;
    }

    .footer-burger-accordion h3 {
        font-weight: 300;
        font-size: 18px;
        line-height: 21px;
        text-align: center;
        color: #252525;
        padding: 20px 0 7px;
        margin: 0 20px 0;
        border-bottom: 0.3px solid #252525;
    }

    .footer-burger-accordion h3 svg {
        margin-top: 3px;
        position: absolute;
        right: 10%;
    }

    .footer-burger-rights {
        font-size: 10px;
        font-weight: 200;
        line-height: 12px;
        text-align: center;
        color: #252525;
    }

    .footer-burger-rights a {
        font-size: 10px;
    }

    .footer-burger-accordion li {
        margin-top: 10px;
    }

    .footer-burger-accordion-link {
        text-decoration: none;
        font-weight: 200;
        font-size: 18px;
        line-height: 21px;
        text-align: center;
        color: #252525;
    }
}

