/* catalog */

.catalog {
    margin: 24px 150px 10px;
}

.catalog-mobile {
    display: none;
}

.catalog-container .active {
    color: #CCA88A;
}

.catalog-button-up {
    border: none;
    background-color: transparent;
    position: fixed;
    right: 3%;
    top: 85%;
    cursor: pointer;
}

.catalog-button-up svg path {
    height: 40px;
    width: 40px;
}

.catalog-nav {
    margin-top: 30px;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #252525;
}

.catalog-nav a {
    text-decoration: none;
    color: #252525;
}

.catalog-nav a:hover {
    color: #E0BEA2;
}

.catalog-container {
    display: flex;
    margin-top: 30px;
    margin-bottom: 100px;
}

.catalog-links {
    margin-right: 30px;
    width: 150px;
}

.catalog-links h3 {
    font-weight: 300;
    font-size: 20px;
    color: #252525;
    margin: 0 0 30px;
}

.catalog-link li {
    margin-top: 20px;
    cursor: pointer;
    font-weight: 300;
}

.catalog-link li:hover {
    color: #E0BEA2;
}

.catalog-link .active-li {
    color: #CCA88A;
}

.catalog-products {
    width: 960px;
}

.catalog-products p {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #252525;
}


/* products-list*/
.products-list {
    display: flex;
    flex-wrap: wrap;
}

.products-list a {
    margin-right: 20px;
    text-decoration: none;
}

/* product */

.product {
    width: 280px;
    margin-right: 20px;
    margin-bottom: 20px;
}

.product p {
    margin: 0 0 5px;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #252525;
}

.product-img {
    display: flex;
    align-items: flex-start;
}

.product-img button {
    margin-left: 227px;
    padding: 0;
    position: absolute;
    border: none;
    background: transparent;
    cursor: pointer;
}
.product-img img {
    height: 400px;
    margin-bottom: 15px;
}

.product-img button:hover svg path {
    fill: rgba(161, 19, 19, 0.65);
}

.product-btn-wish svg path{
    fill: rgba(161, 19, 19, 0.65);
    cursor: pointer;
}

.product-price {
    font-weight: 700 !important;
}

.product-color {
    text-align: center;
}

.product-color a {
    text-decoration: none;
    margin: 0;
}

.product-color svg {
    padding-right: 3px;
    padding-left: 3px;
}

@media (max-width: 821px) {
    .catalog {
        margin: 24px 60px 60px;
    }

    .products-list {
        display: flex;
        flex-wrap: wrap;
    }

    .product {
        width: 170px;
        margin-right: 20px;
        margin-bottom: 30px;
    }

    .product p {
        font-size: 14px;
        line-height: 15px;
    }

    .product-img button {
        margin-left: 130px;
    }

    .product-img img {
        height: auto;
        width: 170px;
        margin-bottom: 15px;
    }

    .catalog-button-up {
        right: 1%;
    }

    .catalog-button-up svg {
        width: 40px;
        height: 40px;
    }
}

@media (max-width: 420px) {
    .catalog {
        margin: 26px 20px 0;
    }

    .catalog-mobile {
        display: unset;
    }

    .catalog-products {
        width: auto;
    }

    .catalog-container {
        margin-top: 10px;
        display: flex;
        flex-direction: column;
    }

    .catalog-links {
        display: none;
    }

    .catalog-links-mobile button {
        width: 100%;
        padding-top: 10px;
        padding-bottom: 10px;
        background: #E0BEA2;
        border: none;
        font-size: 18px;
        line-height: 21px;
        color: #FFFFFF;
        font-weight: 300;
    }

    .catalog-links-mobile button svg {
        margin-left: 10px;
        transform: rotate(90deg);
        width: 7px;
        height: 10px;
    }

    .catalog-links-mobile .catalog-link {
        position: absolute;
        text-align: center;
        background: white;
        width: 92%;
        z-index: 1;
    }

    .catalog-links-mobile .catalog-link li {
        margin-bottom: 25px;
        margin-top: 10px;
    }

    .catalog-links-mobile .catalog-link li:nth-child(1) {
        margin-top: 30px;
    }

    .products-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .products-list a {
        margin: 0;
    }

    .product {
        width: 160px;
        margin-right: 0px;
        margin-bottom: 30px;
    }

    .product-img img {
        width: 160px;
    }

    .product-img button {
        margin-left: 120px;
    }

    .catalog-button-up {
        background-color: white;
        z-index: 2;
        border: 1px solid #CCA88A;
        border-radius: 10px;
    }

    .catalog-container {
        margin-bottom: 20px;
    }

}
