.obmen-reason {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 130px;
}

.obmen-reason li {
    width: 165px;
    text-align: center;
}

.obmen-reason li svg {
    margin-bottom: 10px;
}

@media (max-width: 821px) {
    .obmen-reason {
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 70px;
    }

    .obmen-reason li {
        margin-bottom: 20px;
        width: 180px;
        text-align: center;
    }

    .obmen-reason p {
        font-size: 15px;
    }

}

@media (max-width: 420px) {
    .obmen-reason {
        flex-wrap: wrap;
        margin-bottom: 0;
    }

    .obmen-reason li {
        width: 155px;
    }

    .obmen-reason p {
        font-size: 13px;
    }
}
