/* container */
.about {
    margin: 24px 150px 10px;
}

.about-nav {
    margin-top: 30px;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #252525;
}

.about-nav a {
    text-decoration: none;
    color: #252525;
}

.about-nav a:hover {
    color: #E0BEA2;
}

.about-info {
    margin-top: 30px;
}

.about-info h3 {
    font-weight: 300;
    font-size: 20px;
    line-height: 23px;
    margin: 0 0 30px;
}

.about-info p {
    margin: 0 0 10px;
    font-weight: 300;
    font-size: 16px;
    line-height: 20px;
}

.about-text-strong {
    font-weight: 400;
}

.about-photos {
    margin-top: 20px;
    text-align: center;
}

.about-photo {
    width: 270px;
}

@media (max-width: 821px) {

    .about {
        margin: 24px 60px 60px ;
    }

}


@media (max-width: 420px) {
    .about {
        margin: 26px 20px 20px ;
    }
}
