.cart-order {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 10px;
    border-bottom: 0.3px solid #252525;
}

.cart-order-img {
    height: 110px;
}

.cart-order-imid {
    display: flex;
}

.cart-order-ids {
    margin-left: 10px;
    width: 133px;
}

.cart-order-id {
    color: #E0BEA2;
    font-size: 14px !important;
}

.cart-order-name {
    font-size: 14px !important;
}

.cart-order-color {
    border: 0.3px solid rgba(37, 37, 37, 1);
    border-radius: 16px;
    height: 16px;
    width: 16px;
}

.cart-order-color svg {
  padding-left: 1px;
  padding-top: 1px;
}

.cart-order-size {
    padding-right: 2px;
    font-size: 16px;
    line-height: 19px;
    font-weight: 200 !important;
    text-align: center;
    width: 98px;
    height: 50px;
    background-image: url(/src/Assets/Frame.svg);
    background-position: center right;
    background-repeat: no-repeat;
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    border: 0.5px solid #252525;
}

.cart-order-amount {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 94px;
    border: 0.5px solid #252525;
    font-weight: 200;
    padding-top: 10px;
    padding-bottom: 10px;
}
.cart-order-amount span {
    font-size: 19px ;
}

.cart-order-amount button{
    font-size: 24px ;
    margin-right: 2px;
    margin-left: 2px;
    color: #E0BEA2;
}

.cart-order-amount button:hover {
    color: #252525;
}

.cart-bills {
    margin-top: 30px;
    text-align: end;
}

.cart-order button {
    background: transparent;
    border: none;
}

.cart-order button:hover svg path {
    fill: #252525;
}

.cart-order-price {
    font-weight: 700 !important;
    font-size: 18px !important;
}



.cart-info {
    margin-top: 70px;
}

.cart-info h3 {
    font-weight: 300;
    font-size: 20px;
    line-height: 23px;
    margin: 0 0 30px;
}

.cart-info p {
    margin: 0 0 20px;
    font-weight: 300;
    font-size: 16px;
    line-height: 20px;
}

.cart-pay {
    width: 820px;
}

.cart-form {
    display: flex;
    margin-right: 20px;
}

.form-personal {
    display: flex;
    flex-wrap: wrap;
}

.form-personal input {
    border: 0.5px solid rgba(37, 37, 37, 1);
    margin-bottom: 20px;
    margin-right: 20px;
    width: 350px;
    padding-top: 16px;
    padding-bottom: 15px;
}

.form-personal input::placeholder {
    padding-left: 5px;
    font-weight: 200;
    font-size: 16px;
    line-height: 19px;
    color: rgba(37, 37, 37, 0.5);
}

.form-delivery {
    display: flex;
}

.form-delivery-country {
    width: 372px;
    font-weight: 200;
    margin-bottom: 20px;
}

.form-delivery-country p{
    margin-bottom: 0;
}

.form-delivery-country input {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #252525;
    margin-top: 20px;
}

.form-delivery-country label {
    position: relative;
    bottom: 2px;
}

.form-delivery-earth label{
    position: relative;
    bottom: 2px;
}

.radio {
    margin-right: 4px;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 17px;
    width: 17px;
    border-radius: 17px;
    border: 0.3px solid rgba(37, 37, 37, 1);
    background-color: transparent;
    cursor: pointer;
    box-sizing: border-box;
}

.radio:hover {
    background-color: #f9f9f9;
    box-shadow: inset 1px 2px 3px 0 rgba(0,0,0,0.1);
}

.radio:checked{
    border-color: #555;
    background-color: #E0BEA2;
    box-shadow: inset 1px 2px 3px 0 rgba(0,0,0,0.1);
    cursor: default;
}

.form-delivery-earth {
    font-weight: 200 !important;
}

.form-delivery-earth p{
    margin-bottom: 0;
}

.form-delivery-earth input {
    margin-top: 20px;
}

.form-address input {
    border: 0.5px solid rgba(37, 37, 37, 1);
    margin-bottom: 20px;
    margin-right: 20px;
    width: 720px;
    padding-top: 16px;
    padding-bottom: 15px;
}

.form-address input::placeholder {
    padding-left: 5px;
    font-weight: 200;
    font-size: 16px;
    line-height: 19px;
    color: rgba(37, 37, 37, 0.5);
}

.cart-pay-info {
    width: 300px;
}

.cart-pay-info li {
    margin-bottom: 20px;
}

.cart-pay-info a {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    text-decoration-line: underline;
    color: #252525;
}

.cart-pay-info-end p {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #252525;
}

.cart-pay-info-end button {
    cursor: pointer;
    border: 0;
    background: #E0BEA2;
    align-items: center;
    padding: 16px 70px 17px;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 15px;
}

.cart-pay-info-end button:hover {
    background: #CCA88A;
}

.cart-pay-info-end button a {
    margin: 0;
    color: #FFFFFF;
    text-decoration: none;
}

.cart-pay-info-end span {
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
}

.order {
    display: flex;
}

.order-info {
    width: 500px;
}

.order-info h4 {
    margin: 0 0 10px;
    color: #CCA88A;
}

.order-info-contacts {
    margin-bottom: 20px;
}

.order-info-pic {
    display: flex;
    flex-wrap: wrap;
}

.cart-order-imid.order-pic {
    margin-right: 8px !important;
}
.order-info p {
    font-weight: 600;
    margin: 0 0 10px;
    font-size: 14px;
    line-height: 19px;
    color: #252525;
}

.p-bold {
    font-weight: 600;
    margin-left: 10px;
}

.order-info-button {
    cursor: pointer;
    border: 0;
    background: #E0BEA2;
    align-items: center;
    padding: 16px 70px 17px;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    text-transform: uppercase;
    margin-right: 15px;

}

.order-info-button:hover {
    background: #CCA88A;
}



@media (max-width: 821px) {
    .cart-form {
        width: 400px;
        flex-direction: column;
    }

    .cart-pay-info {
        width: 680px;
        margin-top: 40px;
        display: flex;
        justify-content: space-between;
        border: 0.5px solid #342d27;
        padding: 20px;
    }

    .cart-pay-info-end {
        width: 300px;
    }

    .cart-pay-info-end p {
        margin-bottom: 11px !important;
    }

    .cart-pay-info-end button {
        width: 300px;
    }

    .form-delivery-country label, .form-delivery-earth label, .cart-pay-info-end button a  {
        font-size: 14px;
    }

    .form-delivery {
        flex-wrap: wrap;
        margin-bottom: 30px;
    }

    .form-delivery-country.money {
        margin-bottom: 0 !important;
    }

    .form-address {
        margin-top: 20px;
    }

    .cart-info p {
        margin-bottom: 0 ;
    }

    .cart-header {
        margin-bottom: 20px !important;
    }
}

@media (max-width: 420px) {
    .cart-order {
        display: flex;
    }

    .cart-order-name {
        font-size: 14px !important;
    }

    .cart-order-ids {
        width: 90px;
    }

    .cart-order-id {
        font-size: 12px !important;
        margin-bottom: 5px !important;
    }

    .cart-order-color {
        position: relative;
        right: -15px;
        top: -10px;
        margin: 0;
    }

    .cart-order-color svg {
        padding-right: 2px;
    }

    .cart-order-size {
        padding: 12px;
        height: 40px;
        margin: 0;
        left: 25px;
        top: 30px;
        position: relative;
        font-size: 14px;
        background-image: none;
    }

    .cart-order-amount {
        position: relative;
        top: 30px;
        height: 28.5px;
        left: 40px;
        width: 67px;
        padding-top: 5px;
        padding-bottom: 5px;
    }

    .cart-order-price {
        font-size: 15px !important;
        position: relative;
        bottom: 20px;
        right: 70px;
    }

    .cart-order-delete-btn {
        position: relative;
        bottom: 30px;
        right: 36px;
    }

    .cart-form {
        width: 335px;
    }

    .form-personal input, .form-address input{
        width: 335px;
        margin-right: 0;
    }

    .cart-pay {
        width: 335px;
        flex-wrap: wrap ;
    }

    .cart-pay-info {
        width: 335px;
        padding: 0;
        border: unset;
        display: unset;
    }

    .cart-pay-info-end button {
        width: 335px;
    }

    .cart-pay-info-end button {
        margin-top: 15px;
        padding: 16px 104px 17px;
    }

    .order-info {
        width: 300px;
    }

    .cart-order-imid.order-pic {
        margin-right: 0 !important;
        margin-top: 15px;
    }

    .order-info-button {
        width: 335px;
        margin-right: 0;
        margin-bottom: 20px;
    }

}
