/* filtration */

.filtration {
    display: flex;
    margin-bottom: 10px;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
}

.filtration-item {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-right: 80px;
}

.filtration-item-select {
    border: none;
    margin-right: 6px;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #252525;
}

.filtration-item-select .css-13cymwt-control {
    border: none;
}
.filtration-item-select .css-1u9des2-indicatorSeparator {
    display: none;
}

.filtration-item-select .css-t3ipsp-control {
    box-shadow:0 0 0 1px #E0BEA2 ;
    border-color: #E0BEA2;
    justify-self: start;
}

.filtration-item-select .css-t3ipsp-control:hover {
    border-color: #E0BEA2 ;

}

.filtration-item-select .css-Inmdiq5-menu:hover {
    color: #E0BEA2;
}

.filtration-item-select .css-1fdsijx-ValueContainer {
    padding-left: 0;
}

.filtration-item-select .css-qbdosj-Input {
    width: 80px;
}

.filtr-products {
    width: 960px;
}

.filtr-products-list {
    display: flex;
    flex-wrap: wrap;
}

.filtr-products-list p {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
}

@media (max-width: 821px)  {

    .filtration-item-select {
        font-size: 13px;
        line-height: 14px;
        margin-right: 20px;
    }

    .css-1xc3v61-indicatorContainer {
        padding: 2px;
    }

    .filtration-item {
        margin-right: auto;
    }
}

@media (max-width: 420px)  {
    .filtration {
        display: none;
    }

    .mobile-filtration button {
        color: #252525 !important;
        margin-top: 20px;
        border: none;
        background-color: transparent ;
        font-weight: 200 !important;
        font-size: 16px;
        line-height: 19px;
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        padding: 0;
    }

    .mobile-filtration {
        border-bottom: 0.3px solid rgba(37, 37, 37, 1);
        margin-bottom: 10px;
    }

    .mobile-filtration button svg {
        margin-left: 15px;
    }

    .mobile-filtration-container {
        padding-bottom: 10px;
        border-bottom: 0.3px solid rgba(37, 37, 37, 1);
        position: absolute;
        background: white;
        width: 100%;
        z-index: 1;
    }

    .mobile-filtration-item {
        position: relative;
        cursor: pointer;
        display: unset;
        margin-right: auto;
    }

    .mobile-filtration-item-select {
        border: none;
        font-weight: 300;
        font-size: 15px;
        line-height: 17px;
        margin-right: 0;
        color: #252525;
    }

    .mobile-filtration-item-select .css-1xc3v61-indicatorContainer {
        padding: 0 !important;
    }
    .mobile-filtration-item-select .css-15lsz6c-indicatorContainer {
        padding: 0 !important;
    }
    .mobile-filtration-item-select .css-kfbu2a-menu {
        width: 100%;
    }

    .mobile-filtration-item-select .css-13cymwt-control {
        border: none;
    }
    .mobile-filtration-item-select .css-1u9des2-indicatorSeparator {
        display: none;
    }

    .mobile-filtration-item-select .css-t3ipsp-control {
        box-shadow:0 0 0 1px #E0BEA2 ;
        border-color: #E0BEA2;
        justify-self: start;
    }

    .mobile-filtration-item-select .css-t3ipsp-control:hover {
        border-color: #E0BEA2 ;
    }

    .mobile-filtration-item-select .css-Inmdiq5-menu:hover {
        color: #E0BEA2;
    }

    .mobile-filtration-item-select .css-1fdsijx-ValueContainer {
        padding-left: 0;
    }

    .mobile-filtration-item-select .css-qbdosj-Input {
        width: 80px;
    }

    .mobile-filtr-products {
        width: 335px;
    }

    .mobile-filtr-products-list {
        display: flex;
        flex-wrap: wrap;
    }

    .mobile-filtr-products-list p {
        font-weight: 300;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
    }


}
