.header-burger {
    display: none;
    background: transparent;
    border: none;
}

.burger-containers {
    background: #FFFFFF;
    width: 100%;
    height: 620px;
}

.burger-list {
    font-weight: lighter;
    font-size: 22px;
    line-height: 19px;
    text-align: center;
    color: #252525;
    margin-top: 30px;
}

.burger-list a {
    text-decoration: none;
    color: #252525;
}

.burger-list li {
    border-bottom:  0.3px solid #252525;
    padding-top: 20px;
    padding-bottom: 20px;
}

@media (max-width: 420px) {
    .header-burger {
        display: unset;
    }

    svg.header-logo-beige {
        margin-left: 44px;
        width: 150px;
    }



}

