.oplata-info {
    display: flex;
    margin-bottom: 40px;
}

.oplata-info li {
    margin-right: 80px;
}

.oplata-pay {
    margin-top: 25px;
    display: flex;
    margin-bottom: 170px;
}

.oplata-pay p {
    margin: 0 0 0 10px;
}

.oplata-pay ul {
    display: flex;
    align-items: center;
    margin-right: 40px;
}

@media (max-width: 821px) {
    .oplata-pay p {
        font-size: 13px;
    }

    .oplata-pay {
        margin-bottom: 100px;
    }
}

@media (max-width: 420px) {
    .oplata-info {
        display: inline;
    }

    .oplata-info li {
        margin-right: 0;
        margin-bottom: 30px;
    }

    .oplata-pay {
        display: inline;
    }

    .oplata-pay p {
        margin: 0 0 0 16px;
        font-size: 14px;
    }

    .oplata-pay ul {
        margin-top: 20px;
    }
}

