li, a {
    text-decoration: none;
}

.product-page {
    margin-top: 30px;
    display: flex;
    margin-bottom: 100px;
}

.product-page-photos {
    display: flex;
    width: 500px;
}

.product-page-photos-one {
    width: 100px;
    height: 150px;
    margin-right: 20px;
}

.product-page-photos-two {
    height: 540px;
}

.product-page-text {
    margin-left: 10px;
}

.product-page-name {
    font-weight: 300;
    font-size: 20px;
    line-height: 23px;
}

.product-page-price {
    margin-top: 20px;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
}

.product-page-colors {
    display: flex;
}

.product-page-color {
    height: 30px;
    width: 30px;
    margin-right: 6px;
}

.active svg {
    border: 1px solid rgba(37, 37, 37, 1);
    border-radius: 30px;
}

.product-page-color svg {
    padding: 1px;
    width: 24px;
    height: 24px;
}

.product-page-color-text {
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
    color: #E0BEA2;
}

.product-page-size {
    margin-top: 30px;
    width: 530px;
    padding-left: 19px;
    font-size: 16px;
    line-height: 19px;
    font-weight: 200 !important;
    height: 50px;
    background-image: url(/src/Assets/Frame.svg);
    background-position: center right;
    background-repeat: no-repeat;
}

.product-page-btns {
    display: flex;
}

.product-page-btns button {
    cursor: pointer;
    background: transparent;
    border: none;
    margin-top: 20px;
    width: 255px;
    height: 50px;
}

.product-page-btn-cart {
    padding: 0;
    background: #E0BEA2 !important;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    margin-right: 20px;
}

.product-page-btn-cart:hover {
    background: #CCA88A !important;
}

.product-page-btn-like {
    border: 0.5px solid rgba(37, 37, 37, 1) !important;
    font-weight: 300;
    font-size: 16px;
    text-align: center;
    text-transform: uppercase;
    color: #252525;
    display: flex;
    align-items: center;
    justify-content: center;
}

.product-page-btn-like svg {
    margin-right: 5px;
}

.product-page-btn-like:hover {
    background: rgba(221, 221, 221, 0.82) !important;
}

.product-btn-like-wish {
    background: rgba(221, 221, 221, 0.82) !important;
}

.product-page-about {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    margin-top: 20px;
    color: #252525;
}

/* product-page accordion */

.product-page-accordion h3 {
    margin: 0;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #252525;
}

.product-page-accordion-svg {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    padding-bottom: 10px;
    border-bottom: 0.3px solid #252525;
    text-align: center;
}

.product-page-accordion p {
    font-weight: 200;
    font-size: 14px;
    line-height: 16px;
    color: #252525;
    width: 530px;
}

/* product-page early */

.product-page-early {
    font-weight: 300;
    font-size: 20px;
    line-height: 23px;
    color: #252525;
    margin-bottom: 30px;
}

.product-page-early-item {
    display: flex;
    flex-wrap: wrap;
}

.product-page-early-item .product {
    margin-right: 5px;
}


@media (max-width: 821px)  {

    .product-page {
        margin-bottom: 50px;
    }

    .product-page-photos {
        width: 300px;
    }

    .product-page-photos-one {
        width: 80px;
        margin-right: 5px;
    }

    .product-page-photos-two {
        height: 300px;
    }

    .product-page-name {
        font-size: 20px;
        margin: 0;
    }

    .product-page-price {
        font-size: 16px;
        line-height: 19px;
    }

    .product-page-size {
        width: 410px;
    }

    .product-page-btns button {
        width: 200px;
    }

    .product-page-btn-cart {
        margin-right: 10px;
    }

    .product-page-accordion p {
        width: 410px;
    }
}

@media (max-width: 420px)  {

    .product-page {
        display: flex;
        flex-direction: column;
    }

    .product-page-photos {
        width: 340px;
        margin-bottom: 20px;
    }

    .product-page-photos-one {
        width: 100px;
        margin-right: 5px;
    }

    .product-page-photos-two {
        height: 340px;
    }

    .product-page-name {
        font-size: 20px;
        margin: 0;
    }

    .product-page-price {
        font-size: 16px;
        line-height: 19px;
    }

    .product-page-size {
        width: 322px;
    }

    .product-page-btns button {
        width: 322px;
    }

    .product-page-btn-cart {
        margin-right: 10px;
        font-size: 13px;
    }

    .product-page-btn-like {
        font-size: 13px;
    }

    .product-page-btn-like svg {
        width: 25px;
        height: 25px;
    }

    .product-page-accordion p {
        width: 322px;
    }
}
