li, a {
    text-decoration: none;
}

.header-nav-link {
    text-decoration: none;
    color: #252525;
}

.header-nav-link:hover {
    cursor: pointer;
    color: #E0BEA2;
}

.header-nav li:hover {
    cursor: pointer;
    color: #E0BEA2;
}

.header-nav {
    display: flex;
    align-items: center;
    font-weight: 300;
}

.header-nav li {
    margin-right: 60px;
}

.header-logo-beige {
    margin-left: 60px;
    margin-right: 120px;
}

.active {
    color: #E0BEA2;
}

.header-controls button .active svg {
    border: none !important;
    border-radius: 0 !important;
}

.header-controls button .active path {
    fill: #252525;
}

.header-controls button:hover path {
    cursor: pointer;
    fill: #252525;
}

.header-controls button {
    padding-right: 10px;
    padding-left: 10px;
    background: transparent;
    border: none;
    cursor: pointer;
}

.header-nav-link span {
    font-size: 11px;
}

@media (max-width: 821px) {

    .header {
        margin: 10px 0 0;
    }

    .header-nav {
        justify-content: start;
        font-size: 15px;
    }
    .header-nav li {
        margin-right: 13px;
    }

    .header-logo-beige {
        height: 30px;
        margin: 0 7px 0 0;
    }

    .header-controls button {
        padding-right: 7px;
        padding-left: 7px;
        background: transparent;
        border: none;
        cursor: pointer;
    }

}


@media (max-width: 420px) {
    .header-nav {
        justify-content: space-between;
    }

    .header-nav-link {
        display: none;
    }

    .header-nav li {
        display: none;
    }

    .header-nav svg {
        height: 20px;
    }

    .header-controls  .header-nav-link  {
        display: unset;
    }

    .header-controls button {
        padding: 0;
    }

    .header-controls button:last-child {
        padding-left: 10px;
    }

}
